@import url('https://fonts.googleapis.com/css2?family=DynaPuff&display=swap');

:root {
    --color-blue-light: #8b9bf5;
    --color-blue: #4f65e4;
    --color-blue-dark: #192eaa;
    --color-orange-light: #d08523;
    --color-orange: #c77c1b;
    --color-orange-dark: #5e3500;
    --color-yellow-light: #f0dd2d;
    --color-yellow: #e6d118;
}

* {
    box-sizing: border-box;
    font-family: inherit;
    margin: 0;
    padding: 0;
    transition: 0.25s;
    user-select: none;
}

body {
    background: linear-gradient(var(--color-yellow), var(--color-orange)) no-repeat;
    color: black;
    height: 100%;
    font-family: 'DynaPuff', cursive;
    overflow: hidden;
    transition: 0s;
}

footer {
    bottom: 0;
    color: var(--color-yellow);
    font-size: 2em;
    padding: 32px;
    position: fixed;
    text-align: center;
    text-shadow: 0 0 8px var(--color-orange-dark);
    width: 100%;
}

footer:hover {
    font-size: 4em;
}

html {
    height: 100%;
}

nav {
    background: linear-gradient(var(--color-blue-dark), var(--color-blue));
    border-bottom: 1px solid var(--color-blue-light);
    box-shadow: 0 8px 32px var(--color-orange);
    display: flex;
    flex-direction: column;
    gap: 32px;
    padding: 32px;
}

section {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-size: 6em;
    gap: 32px;
    margin-top: 64px;
    justify-content: center;
    text-shadow: 4px 4px 8px var(--color-orange);
}

.minimize-navbar {
    margin-top: -80px;
    gap: 0;
}

.hide {
    opacity: 0;
    pointer-events: none;
}

.prompt {
    align-items: center;
    animation-iteration-count: infinite;
    animation-duration: 8s;
    animation-name: slide;
    color: var(--color-blue-light);
    display: flex;
    font-size: 4em;
    gap: 0.5em;
    justify-content: center;
    position: relative;
    text-align: center;
    text-shadow:
    0 0 2px var(--color-blue-dark),
    0 0 4px var(--color-blue-dark),
    0 0 8px var(--color-blue-dark),
    0 0 16px var(--color-blue-dark),
    0 0 32px var(--color-blue-dark);
    width: 100%;
}

.question-type-buttons-bar {
    align-items: center;
    display: flex;
    justify-content: space-evenly;
}

.options-bar {
    align-items: center;
    display: flex;
    justify-content: space-evenly;
    margin-top: 32px;
}

.options-button-tray-container {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.options-button-tray-label {
    color: var(--color-orange-dark);
    font-size: 1.25em;
    padding-left: 8px;
}

.mode-button-tray {
    align-items: center;
    border-radius: 16px;
    box-shadow: 0 8px 32px var(--color-orange);
    color: var(--color-blue-light);
    display: flex;
    justify-content: center;
}

.mode-button {
    background: linear-gradient(var(--color-blue-dark), var(--color-blue));
    border: 1px solid var(--color-blue-light);
    font-size: 2.5em;
    padding: 8px 16px;
    overflow: hidden;
}

.mode-button:first-child {
    border-radius: 16px 0 0 16px;
}

.mode-button:last-child {
    border-radius: 0 16px 16px 0;
}

.mode-button:hover {
    background: linear-gradient(#8b9bf5, #4f65e4);
    border: 1px solid #ddddff;
    box-shadow: 0 0 16px #ddddff;
    color: #ddddff;
    cursor: pointer;
    z-index: 2;
}

.mode-button-tray > .selected {
    background: linear-gradient(#8b9bf5, #4f65e4);
    border: 1px solid var(--color-blue-light);
    box-shadow: 0 0 0 var(--color-blue-light);
    color: #ddddff;
    cursor: default !important;
    z-index: 1 !important;
}

.mode-button > p {
    scale: 0.9;
}

.mode-button:hover > p {
    scale: 1;
}

.mode-button-tray .selected > p {
    scale: 1 !important;
}

.question-type-button {
    align-items: center;
    background: linear-gradient(var(--color-blue), var(--color-blue-dark));
    border: 1px solid var(--color-blue-light);
    border-radius: 16px;
    box-shadow: 0 0 8px var(--color-blue-dark);
    color: var(--color-blue-light);
    display: flex;
    font-size: 8em;
    height: 1em;
    justify-content: center;
    scale: 0.9;
    width: 1em;
}

.question-type-button:hover {
    --color-blue-dark: #4f65e4;
    --color-blue: #8b9bf5;
    --color-blue-light: #ddddff;
    box-shadow: 0 0 16px var(--color-blue-light);
    cursor: pointer;
    scale: 1;
}

.equation {
    align-items: center;
    display: flex;
    font-size: 1.5em;
    gap: 64px;
    justify-content: center;
}

.equation > p:hover {
    scale: 1.25;
}

.answer {
    align-items: center;
    border: 0 solid var(--color-yellow-light);
    border-radius: 16px;
    box-shadow: 0 0 0 var(--color-yellow-light);
    display: flex;
    justify-content: center;
}

.answer:hover {
    scale: 1.25;
}

.answer-button {
    background: linear-gradient(var(--color-yellow), var(--color-orange)) no-repeat;
    border: 1px solid var(--color-orange-dark);
    box-shadow: 0 0 16px var(--color-orange-dark);
    height: 1em;
    width: 1em;
}

.answer-button:hover {
    background: linear-gradient(var(--color-yellow-light), var(--color-orange-light)) no-repeat;
    border: 1px solid var(--color-yellow-light);
    box-shadow: 0 0 32px var(--color-yellow-light);
    color: var(--color-orange-dark);
    cursor: pointer;
}

.bounce {
    animation-fill-mode: both;
    animation-iteration-count: 1;
    animation-duration: 2000ms;
    animation-name: bounce;
}

.float {
    animation-iteration-count: infinite;
    animation-duration: 1s;
    animation-name: float;
}

.rock {
    animation-iteration-count: infinite;
    animation-duration: 4s;
    animation-name: rock;
}

.rock-more {
    animation-iteration-count: infinite;
    animation-duration: 2s;
    animation-name: rock-more;
    scale: 1.25;
}


@keyframes bounce {
    0% { scale: 2; opacity: 0;}
    100% { scale: 1; opacity: 1; }
}

@keyframes float {
    0% { margin-top: 0; }
    50% { margin-top: 32px; }
    100% { margin-top: 0; }
}

@keyframes rock {
    0% { transform: rotate(2deg); }
    50% { transform: rotate(-2deg); }
    100% { transform: rotate(2deg); }
}

@keyframes rock-more {
    0% { transform: rotate(15deg); }
    50% { transform: rotate(-15deg); }
    100% { transform: rotate(15deg); }
}

@keyframes slide {
    0% { left: 15%; }
    50% { left: -15%; }
    100% { left: 15%; }
}